import Data from './experience.json';

const Experience = () => {
    return(
        <>
        { Data.map((exp) => (
            <div key={exp.id} className="resume-item">
                <h4>{exp.jabatan}</h4>
                <h5>{exp.tahun}</h5>
                <p><em>{exp.nama}</em></p>
                <p>{exp.detail}</p>
            </div>
        ))}
        </>
    )
}

export default Experience;